<template>
  <div>
    <v-card class="pb-4" height="60">
      <div class="mt-5 mx-5">
        <template v-if="$auth.check(['ADMIN', 'RESPONSIBLE'])">
          <v-btn class="mr-2 my-2" @click="create()" color="#007bff" dark>
            <v-icon left>mdi-briefcase-plus</v-icon>
            {{ $vuetify.lang.t("$vuetify.btn.add") }}
          </v-btn>
        </template>
        <v-btn
          :loading="loading.refresh"
          @click="refreshItems()"
          class="mr-2 my-2"
          depressed
          color="#f1f5fc"
        >
          <v-icon left color="#007bff">mdi-refresh</v-icon>
          {{ $vuetify.lang.t("$vuetify.btn.refresh") }}
        </v-btn>
        <v-btn
          color="#f1f5fc"
          @click="showFilter()"
          class="mr-2 my-2"
          depressed
        >
          <v-icon color="#007bff" left>mdi-filter-outline</v-icon>
          {{ $vuetify.lang.t("$vuetify.supplies.btn.filter.title") }}
        </v-btn>
        <v-spacer></v-spacer>
      </div>
    </v-card>
    <v-card>
      <v-data-table
        :footer-props="{
          'items-per-page-options': itemPerPageOptions
        }"
        :headers="headers"
        :items="items"
        :items-per-page="itemPerPage"
        :loading="loading.list"
        :options.sync="pagination"
        :server-items-length="meta.totalElements"
        :sort-by="sortBy"
        :sort-desc="sortDesc"
        item-key="name"
        class="mt-5"
      >
        <template v-slot:header.code="{ header }">
          <tr>
            {{
              $vuetify.lang.t(header.text)
            }}
          </tr>
        </template>
        <template v-slot:header.arrival_date="{ header }">
          <tr>
            {{
              $vuetify.lang.t(header.text)
            }}
          </tr> </template
        ><template v-slot:header.storage_depot="{ header }">
          <tr>
            {{
              $vuetify.lang.t(header.text)
            }}
          </tr>
        </template>
        <template v-slot:header.supply_amount="{ header }">
          <tr>
            {{
              $vuetify.lang.t(header.text)
            }}
          </tr>
        </template>
        <template v-slot:header.reference_no="{ header }">
          <tr>
            {{
              $vuetify.lang.t(header.text)
            }}
          </tr> </template
        ><template v-slot:header.additional_info="{ header }">
          <tr>
            {{
              $vuetify.lang.t(header.text)
            }}
          </tr>
        </template>

        <template v-slot:header.null="{ header }">
          <tr>
            {{
              $vuetify.lang.t(header.text)
            }}
          </tr>
        </template>
        <template v-slot:body="{ items }">
          <v-hover>
            <tbody slot-scope="{ hover }">
              <tr :key="item.id" v-for="item in items">
                <td class="subtitle-2">{{ item.code }}</td>

                <td class="subtitle-2">
                  {{ $moment(item.arrival_date).format("YYYY-MM-DD") }}
                </td>
                <td class="subtitle-2">
                  {{ item.storage_depots.name }}
                </td>
                <td class="subtitle-2">
                  {{ item.supply_amount }}
                </td>
                <td class="subtitle-2">
                  {{ item.reference_no }}
                </td>
                <td class="subtitle-2">
                  {{ item.additional_info }}
                </td>
                <td
                  class="subtitle-2"
                  v-if="$auth.check(['ADMIN', 'RESPONSIBLE'])"
                >
                  <template>
                    <v-tooltip bottom>
                      <template
                        v-slot:activator="{ on }"
                        v-if="$auth.check(['ADMIN', 'RESPONSIBLE'])"
                      >
                        <v-btn
                          @click="editSupplies(item)"
                          color="secondary"
                          icon
                          text
                          v-on="on"
                          v-show="$vuetify.breakpoint.smAndDown || hover"
                        >
                          <v-icon>mdi-briefcase-plus</v-icon>
                        </v-btn>
                      </template>
                      <span>{{ $vuetify.lang.t("$vuetify.btn.edit") }}</span>
                    </v-tooltip>
                    <!--<v-tooltip bottom>-->
                    <!--<template-->
                    <!--v-slot:activator="{ on }"-->
                    <!--v-if="$auth.check(['ADMIN', 'RESPONSIBLE'])"-->
                    <!--&gt;-->
                    <!--<v-btn-->
                    <!--@click="deleteItem(item)"-->
                    <!--color="red"-->
                    <!--icon-->
                    <!--text-->
                    <!--v-on="on"-->
                    <!--v-show="$vuetify.breakpoint.smAndDown || hover"-->
                    <!--dark-->
                    <!--&gt;-->
                    <!--<v-icon>mdi-delete-outline</v-icon>-->
                    <!--</v-btn>-->
                    <!--</template>-->
                    <!--<span>{{ $vuetify.lang.t("$vuetify.btn.remove") }}</span>-->
                    <!--</v-tooltip>-->
                  </template>
                </td>
                <td class="subtitle-2" v-else></td>
              </tr>
            </tbody>
          </v-hover>
        </template>
      </v-data-table>
      <form-supplies
        :dialog="dialogForm"
        :form="form"
        :messages="messages"
        @getItems="getItems"
        ref="suppliesForm"
      ></form-supplies>
      <delete-item
        :dialog="dialogDelete"
        :messages="messagesDelete"
        :urlItems="urlDeleteItems"
        @getItems="getItems"
      ></delete-item>
      <filter-form
        ref="filterForm"
        :dialog="dialogForm"
        :filter="filter"
        @performFilter="performFilter"
      ></filter-form>
    </v-card>
  </div>
</template>

<script>
import ListMixin from "./../../mixins/Common/List.vue";
import FormSupplies from "./Form";
import FilterForm from "./Filter";
import DeleteItem from "./../Common/Delete";

export default {
  mixins: [ListMixin],
  data: () => ({
    urlItems: "supplies",
    sortBy: ["id"],
    dialogForm: {
      show: false,
      shows: {
        showFilter: false,
        showInfo: false
      }
    },
    dialogDelete: {
      show: false
    },
    dialogDetail: {
      show: false
    },
    dialogEnt: {
      show: false
    },
    form: {
      id: null,
      pasysto: null,
      padiasto: null,
      temperature: null,
      bpm: null,
      size: null,
      electrophoresis: null,
      weight: null,
      blood_group: null,
      allergies: null,
      pathologies: null,
      name: null,
      religion: null,
      type_id: null,
      mother_name: null,
      emergency_name: null,
      emergency_contact: null,
      maritalStatus: null,
      number_id: null,
      email: null,
      date_id: null,
      child: null,
      dateNaiss: null,
      reference_no: null,
      other_phone: null,
      phone: null,
      insurance_name: null,
      insurance_number: null,
      code: null,
      age: null,
      gender: null,
      shape: null
    },
    filter: {
      name: null,
      role: null
    },
    messages: {
      title: "",
      submit: "",
      success: ""
    },
    messagesDelete: {
      success: "$vuetify.supplies.delete.success"
    }
  }),
  methods: {
    setHeaders() {
      this.headers = [
        {
          text: "$vuetify.supplies.cols.code.title",
          value: "code",
          align: "start",
          sortable: false,
          width: "15",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.supplies.cols.arrival_date.title",
          value: "arrival_date",
          align: "start",
          sortable: false,
          width: "50",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.supplies.cols.storage_depot.title",
          value: "storage_depot",
          align: "start",
          sortable: false,
          width: "50",
          class: "grey--text text--darken-3"
        },

        {
          text: "$vuetify.supplies.cols.supply_amount.title",
          value: "supply_amount",
          align: "start",
          sortable: false,
          width: "30",
          class: "grey--text text--darken-3"
        },

        {
          text: "$vuetify.supplies.cols.reference_no.title",
          value: "reference_no",
          align: "start",
          sortable: false,
          width: "6",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.supplies.cols.additional_info.title",
          value: "additional_info",
          align: "start",
          sortable: false,
          width: "6",
          class: "grey--text text--darken-3"
        },
        {
          text: "$vuetify.supplies.cols.actions.title",
          value: null,
          align: "start",
          sortable: false,
          width: "140"
        }
      ];
    },
    create() {
      this.dialogForm.show = true;
      this.messages = {
        title: "$vuetify.supplies.new.title",
        submit: "$vuetify.supplies.new.submit",
        success: "$vuetify.supplies.new.success"
      };
      this.form = {
        id: null,
        pasysto: null,
        padiasto: null,
        temperature: null,
        bpm: null,
        size: null,
        electrophoresis: null,
        weight: null,
        blood_group: null,
        allergies: null,
        pathologies: null,
        name: null,
        religion: null,
        type_id: null,
        mother_name: null,
        emergency_name: null,
        emergency_contact: null,
        maritalStatus: null,
        number_id: null,
        email: null,
        date_id: null,
        child: null,
        dateNaiss: null,
        reference_no: null,
        other_phone: null,
        phone: null,
        insurance_name: null,
        insurance_number: null,
        code: null,
        age: null,
        gender: null,
        shape: null
      };
    },
    editSupplies(item) {
      this.dialogForm.show = true;
      this.messages = {
        title: "$vuetify.supplies.update.title",
        submit: "$vuetify.supplies.update.submit",
        success: "$vuetify.supplies.update.success"
      };
      this.$refs.suppliesForm.setForm(item);
    },

    showFilter() {
      this.dialogForm.shows.showFilter = true;
    },
    performFilter() {
      let self = this;
      self.loading.list = true;
      let params = {};
      if (self.filter.suppliesname !== null) {
        params["suppliesname"] = self.filter.suppliesname;
      }
      if (self.filter.role !== null) {
        params["role"] = self.filter.role;
      }
      self._search = params;
      //console.log("FILTRE", this._search);
      self
        .getItems()
        .then(() => {
          self.$refs.filterForm.closeDialog();
        })
        .finally(() => {
          self.$refs.filterForm.stopLoadingBtn();
        });
    }
  },
  components: {
    FormSupplies,
    DeleteItem,
    FilterForm
  }
};
</script>

<style scoped></style>
